import { z } from 'zod';
import { MeasureFormatsEnum, MeasureTypesEnum } from '../type-enums';
import { MetaInterface, MetaSchema } from './meta.interface';

export const MeasureSchema = z
  .object({
    name: z.string().min(1),
    description: z.string().min(1).optional(),
    type: z.nativeEnum(MeasureTypesEnum),
    format: z.nativeEnum(MeasureFormatsEnum).optional(),
    index: z.string().optional(),
    sql: z.string().min(1).optional(),
    dimension: z.string().min(1).optional(),
    public: z.boolean().optional(),
    meta: MetaSchema.optional(),
  })
  .refine(
    (data) =>
      !(
        (data.sql === undefined && data.dimension === undefined) ||
        (data.sql !== undefined && data.dimension !== undefined)
      ),
    {
      message: 'One of sql or dimension must be defined.',
      path: ['sql', 'dimension'],
    },
  );

export interface MeasureInterface {
  name: string;
  description?: string;
  type: MeasureTypesEnum;
  format?: MeasureFormatsEnum;
  index?: string;
  sql?: string;
  dimension?: string;
  public?: boolean;
  meta?: MetaInterface;
}
